  height: 3px;
  border-radius: 33px;
  background-image: linear-gradient(90deg, transparent, transparent 50%, #fff 50%, #fff 100%), linear-gradient(90deg, #9672FF, #9672FF, #4DDFFD, #4DDFFD, #4DDFFD);
  background-size: 12px 3px, 100% 3px;
  opacity: 0.3;
  border: none;
}

li.list_sec::before {
  content: "";
  position: absolute;
  right: -5px;
  width: 13px;
  height: 13px;
  background: #9672FF;
  top: 40px;
  border-radius: 100%;
